    flex-direction: column;
    min-height: 100vh;
  }
  .header h1{
    font-family: 'Jost', sans-serif;
    text-shadow: 0px 2px 3px #DB7F7F;
    margin-bottom: 0px;
    letter-spacing: 2px;
  }
  .description{
    margin-top: 5px;
  }

  .intro{
    color:white;
    text-align: 'left';
    }
    
    .enter{
