.project-card {
  margin: 20px;
  transition: 0.3s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: white;
  width: 40%;
  border-radius: 70px;
}
.project-info {
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.project-img {
  width: 30%;
  height: 100%;
  object-fit: cover;
  object-position: 20% 50%;
  border-top-left-radius: 2%;
  border-bottom-left-radius: 2%;
}

.project-title {
  font-size: 25px;
  text-align: center;
  color: rgb(192, 101, 121);
  font-weight: bold;
}

.project-card:hover {
  transform: scale(1.05);
}

.desc {
  font-family: 'Open Sans', sans-serif;
  color: rgb(53, 47, 47);
  position: relative;
  height: 80px;
  padding: 15px;
  margin: 0;
  text-align: center;
}

.technologies {
  font-family: 'Open Sans', sans-serif;
  color: rgb(133, 113, 113);
  font-weight: 600;
  background: rgb(255, 255, 255);
  width: 100%;
  text-align: center;
  margin-bottom: auto;
}
.card-footer {
  margin-top: 15px;
  bottom: 10px;
}

.source {
  padding: 15px 20px;
  border-radius: 10px;
  font-weight: bold;
  color: rgb(112, 93, 93);
  background-color: rgb(226, 226, 226);
}

.source:hover {
cursor: pointer;
}


@media only screen and (max-width: 800px){
  .desc {
    display: none;
  }
  .project-card {
    width: 100%;
  }
}

@media only screen and (max-height: 800px){
  .desc {
    display: none;
  }
}
.sidebar {
  position: absolute;
  float: right;
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 96%;
  transform: translate(-50%, -50%);
  position: fixed;
}

button {
  outline: none;
  border: none;
}

.home-button,
.projects-button {
  margin: 5px;
  margin-right: 30px;
  border-radius: 100%;
  padding: 10px;
  opacity: 0.7;
  transition: 0.5s;
}

.home-group,
.projects-group {
  display: flex;
  flex-direction: row-reverse;
}

.home-sidebar,
.projects-sidebar {
  display: inline;
  font-size: 20px;
  color: white;
  margin-right: 10px;
  opacity: 0;
  transition: 0.4s;
}

.home-button:hover + .home-sidebar {
  display: block;
  opacity: 1;
}

.projects-button:hover + .projects-sidebar {
  display: block;
  opacity: 1;
}

.home-button:hover,
.projects-button:hover {
  cursor: pointer;
  opacity: 1;
}

/* tablet and phone screens will NOT show the sidebar */
@media screen and (max-width: 1240px) {
  .sidebar {
    display: none;
  }
}

